.top-menu {
  background-color: var(--color-secondary);

  &__list {
    list-style: none;
    margin: 0;
    padding: 10px 20px;
    display: flex;
    align-items: center;
    gap: 50px;
    font-weight: 700;
    &-item {
    }
    &-link {
      text-transform: capitalize;
    }
  }
  &__icon {
    width: 40px;
    height: 30px;
    display: block;
    object-fit: cover;
    object-position: center;
    overflow: hidden;
  }
}

.bottom-menu {
  display: flex;
  margin-bottom: 30px;
  align-items: center;
  justify-content: center;
  gap: 30px;
  &__item {
    flex: 0 0 75px;
    overflow: hidden;
    position: relative;
    transition: 0.2s;
    cursor: pointer;
    &:hover {
      transform: scale(130%);
    }
  }
  &__img {
    max-width: 100%;
    top: 0;
    left: 0;
  }
  &__caption {
  }
}
