.user {
  background-color: white;
  padding: 25px;
  font-size: 26px;
  text-align: center;
  font-weight: 700;
  max-width: 400px;
  margin: 0 auto;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  &__title {
    margin-bottom: 5px;
  }
  &__btn {
    margin-bottom: 5px;
    cursor: pointer;
    padding: 0;
    position: relative;
    // height: 128px;
    border: none;
    line-height: 0;
    img {
      object-fit: cover;
      object-position: center;
      width: 128px;
      height: 128px;
    }

    &:hover {
      box-shadow: 0 0 5px black;
    }
  }
}
